

/* custom */
.score-circle {
  padding: 5px 10px;
  display: inline-block;
  border-radius: 20px;
  text-align: center;
  line-height: 25px;
  font-weight: bold;
}

.green {
  background-color: green;
  color: white;
}

.yellow {
  background-color: yellow;
  color: black;
}

.red {
  background-color: red;
  color: white;
}
